// You can use ES6 imports here

function animateHeader() {
	var $scrollTop = $(window).scrollTop();
	var $header = $('.header-home');
	if ($scrollTop >= 500) {
		$header.fadeIn('fast');
	} else {
		$header.fadeOut('fast');
	}
}

function animateProjectLexus() {
	var $scrollTop = $(window).scrollTop();
	var $projectContent = $('.project-lexus .project-content');
	var $projectContentOffset = $projectContent.offset().top;
	var $projectContentHeight = $projectContent.height();
	var $offset = $projectContentOffset + $projectContentHeight;
	var $projectBackground = $('.project-lexus .project-background img');

	if ($scrollTop >= $offset) {
		$projectBackground.attr('src', 'assets/img/img-project-lexus_on.jpg');
		$(window).unbind('scroll', animateProjectLexus);
	}
}

function animateProjectNexus() {
	var $scrollTop = $(window).scrollTop();
	var $projectContent = $('.project-tcs');
	var $projectContentOffset = $projectContent.offset().top;
	var $projectContentHeight = $projectContent.height();
	var $offset = $projectContentOffset - 400;
	var $projectImage = $('.project-tcs-nexus');

	if ($scrollTop >= $offset) {
		$projectImage.attr('src', 'assets/img/img-nexus_on.png');
		$(window).unbind('scroll', animateProjectNexus);
	}
}


function projectHover() {
	$('.project-details', this).stop().slideToggle('fast');
}

$(window).scroll(animateHeader);
$(window).scroll(animateProjectLexus);
$(window).scroll(animateProjectNexus);
$('.section-the-rest-content .project').hover(projectHover);
